import React from "react";
import { InputItem, List } from "antd-mobile";
import Currency from './Currency'

const Summary = ({ item }) => (
    <div>
        <List>
            <List.Item extra={item.name}></List.Item>
            <InputItem editable={false} extra={item.mobile}></InputItem>
            <List.Item editable={false} extra={<span>{item.qtyL > 0 ? <span><small>: </small>{item.qtyL} <br /></span> : ''} {item.qtyS > 0 ? <span><small>: </small>{item.qtyS}</span> : ''}</span>}></List.Item>
            <InputItem editable={false} extra={<Currency price={item.priceKo} />}></InputItem>
            <List.Item extra={item.shippingType === 'express' ? '' : ''}></List.Item>
            {item.shippingType !== 'express' ? '' : <List.Item wrap><font color="#888" size="2">{item.address}</font></List.Item>}
        </List>
    </div>
)

export default Summary


