import React from "react";
import { Grid, Badge } from "antd-mobile";

const Catalog = () => (
    <div>
        <Grid
            columnNum={3}
            hasLine={false}
            square={false}
            data={[
                { title: '', size: '17cx25cm', priceKo: '12,000', priceCn: '70' },
                { title: '', size: '17x14cm', priceKo: '8,000', priceCn: '50' },
                {
                    title: '+', priceKo: <span>
                        <Badge text="HOT" /><br /><s>20,000</s><br /><font size='4' color="red">17,000</font></span>, priceCn: <span><s>120</s> <font size='4' color="red">100</font>
                        </span>
                },
            ]}
            renderItem={item => (
                <div>
                    <p><font size="4">{item.title}</font></p>
                    <p>{item.size}</p>
                    <p>{item.priceKo}</p>

                </div>
            )}
        />
        <font size="4">
            <p></p>
            <p>029 13 27693 3</p>
            <p></p>
        </font>
    </div>
)

export default Catalog


